body {
  background-color: #eeeeee;
  font-family: "Lato", sans-serif;
  overflow-x: hidden;
}

a {
  color: #2d3c8e;
}

.wrap {
  display: block;
  position: relative;
}

/* Navigation Overlays */

.main-purple-overlay {
  position: fixed;
  z-index: 6;
  top: 90px;
  left: 0;
  visibility: hidden;
  opacity: 0;
  background-color: #662d91;
  height: calc(100% - 90px);
  width: 100vw;
  transition: visibility 0s linear 0.4s, opacity 0.2s linear 0.2s;
}

.main-purple-overlay.active {
  visibility: visible;
  opacity: 0.6;
  transition: visibility 0s linear 0.2s, opacity 0.2s linear 0.2s;
}

.side-purple-overlay {
  position: fixed;
  z-index: 6;
  top: 90px;
  left: 0;
  visibility: hidden;
  opacity: 0;
  background-color: #662d91;
  height: calc(100vh - 90px);
  width: 250px;
  transition: visibility 0s linear 0.4s, opacity 0.2s linear 0.2s;
