  margin-left: 5px;
}

.sidebar .top-level .slide-out ul li .drop-menu {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  z-index: 7;
  top: 70px;
  left: 0;
  width: 240px;
  background: white;
  transition: visibility 0s linear 0.4s, opacity 0.2s linear 0.2s, top 0.2s linear 0.2s;
}

