  visibility: visible;
  overflow: visible;
  width: 100vw;
  transition: width 1s ease 0s, visibility 0.2s linear 0s;
}

.sidebar .top-level:hover .slide-out li {
  visibility: visible;
  opacity: 1;
  top: 0;
}

